input,
select,
button,
textarea {
  color: inherit;
  font-size: inherit;
  font-family: inherit;
}

// text inputs
input {
  font-family: inherit;
  font-size: 1rem;
  outline: none;
  border-radius: .2em;
  &:not([type='checkbox']):not([type='radio']):not([type='range']) {
    appearance: none;
    padding: .4em .7em;
    border: none;
    width: 100%;
    box-shadow: 0 0 0 1px rgba(#777, .5) inset;
    transition: all var(--ms) ease-out;
  }
  &:active,
  &:focus {
    // box-shadow: 0 0 2px 1px var(--action);
    &:not([type='checkbox']):not([type='radio']):not([type='range']) {
      box-shadow: 0 0 0 1px rgba(#777, .5) inset, 0 0 2px 1px var(--action);
    }
  }
  &[readonly] {
    background: transparent;
    color: inherit;
    box-shadow: none;
    padding: .4em 0;
  }
  &::placeholder {
    font-family: FontAwesome, sans-serif;
  }
  &::-webkit-inner-spin-button {
    margin: -.5em 0;
  }
  &::-webkit-datetime-edit-fields-wrapper {
    margin: -.5em 0;
  }
  &::-webkit-calendar-picker-indicator {
    margin: -.5em 0;
  }
  &::-webkit-datetime-edit {
    max-width: 6em;
  }
}

// range slider
input[type='range'] {
  --handle-color: var(--aqua);
  appearance: none;
  outline: none;
  padding: 0;
  border: 0;
  width: 100%;
  height: .5em;
  border-radius: 1em;
  cursor: pointer;
  box-shadow: 0 0 .3em -.15em #000;
  background:  var(--frost);

  // MOZILLA
  &::-moz-range-track {
    appearance: none;
    background: #fff;
    outline: none;
  }

  &::-moz-focus-outer {
    border: 0;
  }

  &::-moz-range-thumb {
    appearance: none;
    width: 2em;
    height: 2em;
    border: 0;
    // box-shadow: 0 .2em 1em -.3em #000;
    box-shadow: 0 0 0 2px var(--medium-grey);
    border-radius: 1em;
    background: var(--handle-color);
    transform: scale(.7);
    transition: .3s ease-out;
  }

  &::-moz-range-thumb:focus,
  &::-moz-range-thumb:active {
    appearance: none;
    transform: scale(1.1);
  }

  // BLINK/WEBKIT
  &::-webkit-slider-thumb {
    appearance: none;
    width: 2em;
    height: 2em;
    border: none;
    // box-shadow: 0 .2em 1em -.3em #000;
    box-shadow: 0 0 0 2px var(--medium-grey);
    border-radius: 1em;
    background: var(--handle-color);
    transform: scale(.8);
    transition: .3s ease-out;
  }

  &::-webkit-slider-thumb:focus,
  &::-webkit-slider-thumb:active {
    appearance: none;
    transform: scale(1.1);
  }

  &[disabled] {
    opacity: .3;
    cursor: not-allowed;

    &::-moz-range-thumb {
      box-shadow: none;
    }

    &::-webkit-slider-thumb {
      box-shadow: none;
    }
  }
}



